.cell{
    width: 50px;
    height: 50px;
    font-size: 20px;
    border: 1px solid rgba(255, 255, 255, 0.5);
    text-align: center;
    margin-left: 3px;
    margin-right: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cell p{
    margin: 0px;
    margin-bottom: 3px;
    font-weight: normal;
    font-family: custom3;
}

@keyframes flip {
    0% {
        transform: rotateX(0deg);
    }
    25% {
        background-color: white;
    }
    50% {
        transform: rotateX(180deg);
    }

    100% {
        transform: rotateX(0deg);
    }
}